import * as React from "react"
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Table, Dropdown, DropdownButton, Nav, Navbar, Row, Col, Button } from 'react-bootstrap';

const csSubjectsData = require("./cs-2019.json");
const itSubjectsData = require("./it-2019.json");

let curriTitle = "Computer Science"

const IndexPage = () => {

  const [data, setData] = React.useState(csSubjectsData);
  const [selectedSubjects, setSelectedSubjects] = React.useState([]);

  // const addSubject = (subjectCode, subjectGrade) => {
  //   const subject = data.curriculum.subjects.reduce((acc, group) => {
  //     return acc.concat(group.subjects.filter(sub => sub.code === subjectCode));
  //   }, []);

  //   setSelectedSubjects([...selectedSubjects, { ...subject[0], grade: subjectGrade }]);
  // }

  const selectCurriculum = (eventKey) => {
    if (eventKey === "CS") {
      setData(csSubjectsData);
      curriTitle = "Computer Science"
    } else if (eventKey === "IT") {
      setData(itSubjectsData);
      curriTitle = "Information Technology"
    }
  }

  return (
    <>

      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="#home">VMS Curriculum - {curriTitle}</Navbar.Brand>
          <Nav className="me-auto">
            <DropdownButton id="dropdown-basic-button" variant="dark" title="Select Curriculum" onSelect={selectCurriculum}>
              <Dropdown.Item eventKey="CS">CS</Dropdown.Item>
              <Dropdown.Item eventKey="IT">IT</Dropdown.Item>
            </DropdownButton>
          </Nav>
        </Container>
      </Navbar>

      <br></br>

      <Container>
        <Row>
          <Col>
            {data.curriculum.subjects.map((group, groupIndex) => (
              <React.Fragment key={group.groupName}>
                <Table striped bordered hover key={group.groupName}>

                  <thead>
                    <tr>
                      <th colSpan={3}>{group.groupName}</th>
                    </tr>
                    <tr>
                      <th>Code</th>
                      <th>Name</th>
                      <th>Add</th>
                    </tr>
                  </thead>

                  <tbody>
                    {group.subjects.map((subject, subjectIndex) => (
                      <tr key={subject.code}>
                        <td>{subject.code}</td>
                        <td>{subject.name}</td>
                        <td>
                          <Button variant="success" onClick={() => {
                            setSelectedSubjects([...selectedSubjects, { code: subject.code, name: subject.name, grade: 'A' }])
                          }}>
                            Add
                          </Button>
                        </td>
                      </tr>
                    ))}
                  </tbody>

                </Table>
              </React.Fragment>
            ))}
          </Col>

          <Col>
            <h1>Create Semester</h1>
            <Table striped bordered>
              <thead>
                <tr>
                  <th>Code</th>
                  <th>Name</th>
                  <th>Grade</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {selectedSubjects.map((subject) => (
                  <tr key={subject.code}>
                    <td>{subject.code}</td>
                    <td>{subject.name}</td>
                    <td>{subject.grade}</td>
                    <td>
                      <Button variant="danger" onClick={() => {
                        setSelectedSubjects(selectedSubjects.filter(item => item !== subject))
                      }}>
                        Remove
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </Col>


        </Row>
      </Container>
    </>
  );
}

export default IndexPage
